<template>
  <div>
    <b-form-group>
      <label for="skillName">Skill Name</label>
      <b-form-input
      type="text"
      id="skillName"
      required
      v-model="data.skillName"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="skillRating">Skill Rating</label>
      <b-form-rating
      id="skillRating"
      required
      v-model="data.skillRating"></b-form-rating>
    </b-form-group>
  </div>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';
import { FormRatingPlugin, FormInputPlugin, FormGroupPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

Vue.use(FormInputPlugin);
Vue.use(FormGroupPlugin);
Vue.use(FormRatingPlugin);

export default {
  name: 'SkillForm',
  props:['data'],
}
</script>



